=============================*/
body {
  font-family: "Roboto Slab", serif;
  font-weight: normal;
  font-size: 1rem;
  line-height: 1.4;
  color: #333333;
}
@media (min-width: 1920px) {
  body {
    font-size: 1.728rem;
  }
}
@media (min-width: 1680px) and (max-width: 1920px) {
  body {
    font-size: 1.2rem;
  }
}
@media (max-width: 1080px) {
  body {
    font-size: 1.2rem;
  }
}
@media (max-width: 820px) {
  body {
    font-size: 1.2rem;
  }
}
@media (max-width: 570px) {
  body {
    font-size: 1.2rem;
  }
}

h6, h5, h4, h3, h2, h1 {
  font-family: "Proxima Nova";
  font-weight: 400;
  line-height: 1;
  margin-top: 0.5em;
  margin-bottom: 0.5em;
}

/*=====  End of Fonts  ======*/
/*==============================
=            Layout            =
==============================*/
body {
  margin: 0;
  background-color: #f2f2f2;
}
@media (max-width: 570px) {
  body {
    font-size: 1rem;
  }
}

h1 {
  font-size: 2vw;
  color: #000000;
}
@media (max-width: 570px) {
  h1 {
    font-size: 1.4rem;
  }
}

h2 {
  font-size: 2vw;
  color: #000000;
}
@media (max-width: 570px) {
  h2 {
    font-size: 1.2rem;
  }
}

h3 {
  font-size: 1.728rem;
  color: #000000;
}

h4 {
  font-size: 1.2rem;
  color: gray;
  margin-bottom: 0.3rem;
}

h5 {
  font-size: 1.2rem;
  color: #fff;
}

h6 {
  font-size: 1rem;
  color: #fff;
}

.large-text {
  font-size: 1.2rem;
}

.small-text {
  font-size: 0.8rem;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (max-width: 1080px) {
  ul li {
    font-size: 1.2rem;
  }
}

a {
  color: #000000;
  text-decoration: none;
  transition: color 0.3s;
  outline: none;
}

input {
  font-family: "Roboto Slab", serif;
  font-weight: normal;
  font-size: 1rem;
}

svg {
  transition: all 0.3s;
  overflow: visible;
}
svg g {
  transition: all 0.3s;
}
svg path,
svg rect,
svg polygon {
  transition: all 0.3s;
}

/*=====  End of Layout  ======*/
/*==========================================
=            Preloader TweenMax            =
==========================================*/
body.noscroll {
  min-height: 100vh;
  overflow: hidden;
}

#preloader {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: #fff;
  color: #000000;
  z-index: 9999;
}
#preloader .txt {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
#preloader .txt-perc {
  font-size: 35px;
  font-family: "Proxima Nova";
  opacity: 1;
  margin: 0 0 6px 0;
  max-width: 400px;
  text-align: center;
}
#preloader .progress {
  width: 400px;
  height: 10px;
  background-color: rgba(0, 0, 0, 0.3);
  margin: 0 auto 15px auto;
}
#preloader .progress span {
  display: block;
  width: 0;
  height: 100%;
  background-color: #000000;
}
#preloader.is-hidden {
  visibility: hidden;
  opacity: 0;
}

.is-loading {
  overflow: hidden;
}

/*=====  End of Preloader TweenMax  ======*/
/*==============================
=            Layout            =
==============================*/
.wrapper {
  min-height: calc(100vh - 4rem * 2);
  display: block;
  padding: 4rem;
  position: relative;
  background-color: #fff;
  z-index: 1;
}
.wrapper-error {
  width: 100%;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.wrapper-error__number {
  width: 40%;
  min-height: 30vh;
  position: relative;
}
.wrapper-error__number svg {
  width: 100%;
  height: auto;
  stroke: white;
  stroke-width: 5;
  fill: none;
  padding: 1rem;
  transition-duration: 0.3s;
}
.wrapper-error__text {
  width: 100%;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: absolute;
  top: 0;
  left: 0;
}
.wrapper-error__text h2 {
  font-size: 2vw;
  animation: fadeIn 3s ease-in-out;
}
.wrapper-error__button {
  width: 100%;
  min-height: 60px;
  display: flex;
  justify-content: center;
  left: 0;
  top: 15vh;
  position: absolute;
}
.wrapper-error__button .button-back {
  width: 60px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  border: 1px solid #000000;
  background-color: #fff;
  overflow: hidden;
  transition: background-color 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}
.wrapper-error__button .button-back::after {
  content: "";
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  background-color: #000000;
  z-index: 1;
  transition: transform 0.45s cubic-bezier(0.4, 0, 0.2, 1) 75ms;
  transform: scaleX(0);
  transform-origin: center right;
}
.wrapper-error__button .button-back svg {
  fill: none;
  stroke: #000000;
  stroke-width: 6;
  z-index: 10;
  transition: all 0.3s;
}
.wrapper-error__button .button-back:hover svg {
  stroke: #fff;
  transform: translateX(-5px);
}
.wrapper-error__button .button-back:hover::after {
  transform: scaleX(1);
  transition-delay: 0s;
}
.wrapper-info, .wrapper-contact, .wrapper-vendor {
  display: flex;
  flex-direction: row;
}
.wrapper-category {
  display: block;
  padding-right: calc(25% + 50px);
}
.wrapper-cart {
  background-color: #f2f2f2;
}
.wrapper--visible {
  animation: translateIn 1.5s ease-in-out;
}
@media (max-width: 1080px) {
  .wrapper-category {
