.nav-toggle {
  width: 5rem;
  height: 5rem;
  top: 50%;
  display: flex;
  flex-direction: row;
  align-items: center;
  position: fixed;
  cursor: pointer;
  transform: translate(0, -50%);
  transition: transform 0.3s;
  z-index: 9800;
}
.nav-toggle #toggleBack {
  width: 5rem;
  height: 5rem;
  position: absolute;
  transform: translateX(-50%);
}
.nav-toggle .menu-back {
  width: 5rem;
  height: 5rem;
  fill: #fff;
  position: absolute;
}
.nav-toggle #menuIcon {
  width: calc(4rem / 2);
  height: calc(4rem / 2);
  position: absolute;
  padding: calc(4rem / 4);
  transform: rotate(90deg);
  transform-origin: 50% 50%;
}
.nav-toggle #menuUp {
  transform-origin: center center;
}
.nav-toggle #menuDown {
  transform-origin: center center;
}
.nav-toggle #menuArrow {
  transform-origin: center center;
}
.nav-toggle:hover {
  transform: translate(0.3rem, -50%);
}
.nav-toggle:hover #toggleBack {
  transform: translateX(8%);
}
.nav-toggle:hover #menuIcon {
  transform: translateX(5%);
}
.nav-toggle:hover #menuUp,
.nav-toggle:hover #menuDown {
  transform: translateX(-30%);
}
.nav-toggle:hover #menuArrow {
  transform: translateX(20px);
}
.nav-toggle--open {
  width: 5rem;
  height: 5rem;
  top: 50%;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  cursor: pointer;
  transform: translate(0, -50%);
  transition: transform 0.3s;
  z-index: 9800;
}
.nav-toggle--open #toggleBack {
  width: 5rem;
  height: 5rem;
}
.nav-toggle--open .menu-back {
  width: 5rem;
  height: 5rem;
  fill: #fff;
  position: absolute;
}
.nav-toggle--open #menu_icon_over {
  width: 3rem;
  height: 2rem;
  position: absolute;
  margin-left: 1rem;
}
.nav-toggle--open #menu_up_over {
  fill: #666666;
  transform-origin: center center;
  transform: translateX(50%);
}
.nav-toggle--open #menu_down_over {
  fill: #666666;
  transform-origin: center center;
  transform: translateX(50%);
}
.nav-toggle--open #menu_arrow_over {
  fill: #666666;
  transform-origin: center center;
  transform: translateX(-15px);
  opacity: 0;
}
.nav-toggle--open:hover #menu_icon_over {
  transform: translateX(-10%);
}
.nav-toggle--open:hover #menu_up_over,
.nav-toggle--open:hover #menu_down_over,
.nav-toggle--open:hover #menu_arrow_over {
  fill: #000000;
}
.nav-toggle--mobile {
  width: 50px;
  height: 50px;
  display: none;
  visibility: hidden;
  top: 0;
  left: 0;
  position: fixed;
  align-items: center;
  justify-content: center;
  z-index: 9999;
  cursor: pointer;
}
.nav-toggle--mobile .mobile-line {
  width: 30px;
  height: 0.2rem;
  display: block;
  background-color: #000000;
  position: absolute;
}
.nav-toggle--mobile .mobile-line--before {
  width: 30px;
  height: 0.2rem;
  display: block;
  position: absolute;
  background-color: #000000;
  transform: translateY(-300%);
  transform-origin: 50% 50%;
}
.nav-toggle--mobile .mobile-line--after {
  width: 30px;
  height: 0.2rem;
  display: block;
  position: absolute;
  background-color: #000000;
  transform: translateY(300%);
  transform-origin: 50% 50%;
}
.nav-toggle--mobile .mobile-line--open {
  height: 0;
}
.nav-toggle--mobile .mobile-line--open::before {
  transform: rotate(45deg) translateY(0);
}
.nav-toggle--mobile .mobile-line--open::after {
