  stroke: #000000;
  stroke-width: 0.2rem;
  transform-origin: 50% 50%;
  transition: transform 0.2s;
}
nav .item-has-children > a.submenu-open svg#navCross #navCrossVert {
  transform: rotate(90deg);
}
nav .item-has-children > a.submenu-open svg#navCross #navCrossHorz {
