  color: #666666;
}
nav#nav-third ul li a.active {
  color: #fff;
}
nav#nav-third ul li a.active:after {
  background-color: #fff;
}
nav#nav-third ul li a.active:hover {
  color: #fff;
}
nav#nav-third ul li a:hover {
  color: gray;
}
