/*===============================
=            Product            =
===============================*/
/* Product */
.ms2_product {
  margin-bottom: 10px;
}

.ms2_product .price {
  padding-left: 50px;
  padding-right: 30px;
}

.ms2_product .old_price {
  padding-right: 30px;
}

.ms2_product .tags, .ms2_product .color, .ms2_product .size {
  padding-right: 20px;
  font-size: 0.9em;
}

#msProduct .old_price, #msCart .old_price, .ms2_product .old_price {
  color: gray;
  font-style: italic;
  text-decoration: line-through;
}

.product-content {
  width: 100%;
  position: relative;
}
.product-content__gallery {
  width: calc(50% - 50px);
  height: calc(100vh - 100px);
  background-color: #fff;
  position: fixed;
  overflow: hidden;
}
.product-content__gallery img {
  width: auto;
  height: 100%;
  display: block;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.product-content__options {
  width: calc(50% - 10rem);
  left: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #000000;
  color: #cccccc;
  position: relative;
  padding: 0 5rem;
}
.product-content__options .options-header {
  width: 100%;
  height: calc(45vh - 50px);
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.product-content__options .options-header__breadcrumbs ul.breadcrumb {
  width: 100%;
  height: 2rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 1rem 0;
}
.product-content__options .options-header__breadcrumbs ul.breadcrumb li {
  background-color: #000000;
  z-index: 1;
}
.product-content__options .options-header__breadcrumbs ul.breadcrumb li a {
  font-size: 0.8rem;
  height: 2rem;
  display: flex;
  align-items: center;
  color: gray;
  background-color: #000000;
  transition: all 0.3s;
}
.product-content__options .options-header__breadcrumbs ul.breadcrumb li a:hover {
  color: #fff;
  border-color: #fff;
}
.product-content__options .options-header__breadcrumbs .breadcrumbs-arrow {
  width: 1rem;
  height: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  margin-top: -1rem;
  padding: 0.5rem;
  transform: translate(0, -100%) rotate(-45deg);
  transition: all 0.3s;
  opacity: 0;
}
.product-content__options .options-header__breadcrumbs .breadcrumbs-arrow svg {
  width: 1rem;
  height: 1rem;
}
.product-content__options .options-header__breadcrumbs .breadcrumbs-arrow svg polyline,
.product-content__options .options-header__breadcrumbs .breadcrumbs-arrow svg line {
  fill: none;
  stroke-width: 3;
  stroke: #fff;
}
.product-content__options .options-header__breadcrumbs:hover .breadcrumbs-arrow {
  transform: translate(-120%, -100%) rotate(-45deg);
  opacity: 1;
}
.product-content__options .options-header__vendor a {
  color: gray;
}
.product-content__options .options-header__vendor a:hover {
  color: #fff;
}
.product-content__options .options-header__title {
  width: calc(100% - 4rem);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding-right: 4rem;
}
.product-content__options .options-header__title h1 {
  color: #cccccc;
  font-size: 2vw;
  text-align: left;
}
.product-content__options .options-header__title h2 {
  color: #cccccc;
  font-size: 1.2rem;
  text-align: left;
}
.product-content__options .options-header__title h3, .product-content__options .options-header__title h4 {
  color: #cccccc;
}
.product-content__options .options-header__title span {
  font-size: 1.2rem;
  color: #cccccc;
}
.product-content__options .options-content {
  width: 100%;
  min-height: 50vh;
  display: flex;
  flex-direction: column;
}
.product-content__options .options-content__top {
  width: 100%;
  height: 5.5rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  border-top: 0.15rem solid gray;
  border-bottom: 0.15rem solid gray;
  z-index: 110;
}
.product-content__options .options-content__top .product-count {
  width: 30%;
  height: 50%;
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
}
.product-content__options .options-content__top .product-count input {
  width: 60%;
  height: 2rem;
  color: #cccccc;
  background-color: transparent;
  border: none;
  outline: none;
  text-align: center;
  font-size: 1.2rem;
}
.product-content__options .options-content__top .product-count__input {
  width: 70%;
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
}
.product-content__options .options-content__top .product-count__unit {
  width: 30%;
  height: 100%;
  display: flex;
  align-items: center;
}
.product-content__options .options-content__top .product-count__up {
  width: 2rem;
  height: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 0;
  right: 0;
  z-index: 2;
  background-color: #000000;
  outline: none;
  border: 0.15rem solid gray;
  padding: 0;
  transition: all 0.3s;
  cursor: pointer;
  border-radius: 0;
}
.product-content__options .options-content__top .product-count__up svg {
  width: 1rem;
  height: 1rem;
}
.product-content__options .options-content__top .product-count__up svg polygon {
  fill: gray;
}
.product-content__options .options-content__top .product-count__up:hover {
  border-color: #fff;
  transform: rotate(45deg);
}
.product-content__options .options-content__top .product-count__up:hover svg {
  transform: rotate(-45deg);
}
.product-content__options .options-content__top .product-count__up:hover svg polygon {
  fill: #fff;
}
.product-content__options .options-content__top .product-count__down {
  width: 2rem;
  height: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 0;
  left: 0;
  z-index: 2;
  background-color: #000000;
  outline: none;
  border: 0.15rem solid gray;
  padding: 0;
  transition: all 0.3s;
  cursor: pointer;
  border-radius: 0;
}
.product-content__options .options-content__top .product-count__down svg {
  width: 1rem;
  height: 1rem;
}
.product-content__options .options-content__top .product-count__down svg rect {
  fill: gray;
}
.product-content__options .options-content__top .product-count__down:hover {
  border-color: #fff;
  transform: rotate(-45deg);
}
.product-content__options .options-content__top .product-count__down:hover svg {
  transform: rotate(45deg);
}
.product-content__options .options-content__top .product-count__down:hover svg rect {
  fill: #fff;
}
.product-content__options .options-content__top .product-price {
  width: 70%;
  height: 100%;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: flex-start;
  font-size: 1.2rem;
}
.product-content__options .options-content__top .product-price .old_price {
  font-size: 1rem;
}
.product-content__options .options-content__bottom {
  width: 100%;
  min-height: calc(80% - 1rem);
  padding: 1rem 0 0;
}
.product-content__options .options-form {
  width: 100%;
  display: flex;
}
.product-content__options .options-form__label {
  width: calc(30% - 1rem);
  min-height: 1rem;
  display: flex;
  justify-content: flex-start;
  padding-right: 1rem;
  font-size: 0.8rem;
}
.product-content__options .options-form__control {
  width: calc(70% - 1rem);
  min-height: 1rem;
  display: flex;
  justify-content: flex-start;
  padding-left: 1rem;
  align-items: center;
  text-align: left;
  margin-bottom: 0.5rem;
  font-size: 0.8rem;
}
.product-content__options .options-form__control select {
  outline: none;
}
.product-content__options .options-form__control a {
  color: gray;
}
.product-content__options .options-form__control a:hover {
  color: #fff;
}
.product-content__options .options-form__input {
  width: 15%;
  height: 2rem;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  position: relative;
  margin-right: 1rem;
  margin-left: 1rem;
}
.product-content__options .options-form__input input {
  width: 100%;
  color: #cccccc;
  background-color: transparent;
  border: none;
  outline: none;
  text-align: center;
}
.product-content__options .options-form__unit {
  width: 20%;
  height: 2rem;
  display: flex;
  align-items: center;
}
.product-content__description {
  width: calc(50% - 10rem);
  min-height: 50vh;
  left: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #000000;
  color: #cccccc;
  position: relative;
  padding: 2rem 5rem 3rem;
}
.product-content__description .content-description__map {
  width: 100%;
  min-height: 0;
  text-align: center;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}
.product-content__description .content-description__map p {
  color: gray;
  margin: 0;
  position: absolute;
  z-index: 0;
}
.product-content__description .content-description__text {
  width: 100%;
  min-height: calc(50vh - 100px - 6rem);
  padding: 3rem 5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.product-content__submit {
  position: fixed;
  bottom: 50%;
  left: 50%;
  transform: translate(-50%, 50%);
  z-index: 100;
}
.product-content__submit button {
  width: 4rem;
  height: 4rem;
  display: flex;
  align-items: center;
  justify-content: center;
  outline: none;
  border: none;
  padding: 0;
  background-color: #000000;
  cursor: pointer;
  transform: rotate(45deg);
}
.product-content__submit button svg#productCartFull {
  width: 2.5rem;
  height: 3.5rem;
  transform: rotate(-45deg) translateX(10%);
}
.product-content__submit button svg#productCartFull #productCart {
  fill: #b3b3b3;
}
.product-content__submit button svg#productCartFull #productCartAdd {
  fill: #b3b3b3;
  transform: translateY(-10%);
}
.product-content__submit button:hover svg#productCartFull #productCart {
  fill: #fff;
}
.product-content__submit button:hover svg#productCartFull #productCartAdd {
  fill: #fff;
  transform: translateY(0);
}
@media (max-width: 1080px) {
  .product-content__options {
    width: calc(50% - 6rem);
    height: calc(100vh - 100px);
    padding: 0 3rem;
  }
  .product-content__options .options-header {
    height: 45vh;
  }
  .product-content__options .options-content {
    height: 55vh;
  }
  .product-content__options .options-content__top {
    height: 10vh;
  }
  .product-content__options .options-content__top .product-count {
    width: 40%;
  }
  .product-content__options .options-content__top .product-count input {
    font-size: 0.8rem;
  }
  .product-content__options .options-content__top .product-price {
    width: 60%;
    font-size: 1rem;
  }
  .product-content__options .options-content__bottom {
    height: 45vh;
  }
  .product-content__options .options-form__label {
    width: calc(40% - 1rem);
    font-size: 0.8rem;
  }
  .product-content__options .options-form__control {
    width: calc(60% - 1rem);
    font-size: 0.8rem;
  }
  .product-content__description {
    width: calc(50% - 6rem);
    left: 50%;
    padding: 0 3rem 2rem;
  }
  .product-content__description .content-description__map {
    display: none;
    visibility: hidden;
  }
  .product-content__description .content-description__text {
    width: 100%;
    padding: 2rem 0;
  }
}
@media (max-width: 820px) {
  .product-content {
    flex-direction: column;
  }
  .product-content__gallery {
    width: 100%;
    min-height: 75vh;
    height: auto;
    position: relative;
  }
  .product-content__options {
    width: calc(100% - 6rem);
    min-height: 50vh;
    height: auto;
    padding: 0 3rem;
    left: 0;
  }
  .product-content__options .options-header {
    min-height: 35%;
    height: auto;
    margin-top: 3rem;
    margin-bottom: 1rem;
  }
  .product-content__options .options-header__breadcrumbs ul.breadcrumb {
    margin: 0;
  }
  .product-content__options .options-header__breadcrumbs .breadcrumbs-arrow {
    display: none;
    visibility: hidden;
  }
  .product-content__options .options-header__title span {
    font-size: 0.8rem;
  }
  .product-content__options .options-content {
    height: auto;
    min-height: 30vh;
    margin-bottom: 2rem;
  }
  .product-content__options .options-content__top {
    height: 10vh;
  }
  .product-content__options .options-content__bottom {
    min-height: 10vh;
    height: auto;
  }
  .product-content__options .options-form__label {
    height: 1rem;
  }
  .product-content__description {
    width: calc(100% - 6rem);
    left: 0;
    padding: 0 3rem 2rem;
  }
  .product-content__description .content-description__text {
    padding-top: 1rem;
  }
  .product-content__submit {
    bottom: 100%;
    position: absolute;
  }
  .product-content__submit button svg#productCartFull {
    transform: rotate(-45deg) translateX(5%);
  }
}
@media (max-width: 570px) {
  .product-content {
    min-height: 100vh;
  }
  .product-content__gallery {
    min-height: 75vh;
  }
  .product-content__options {
    width: calc(100% - 2rem);
    min-height: 100vh;
    height: auto;
    padding: 3rem 1rem 1rem;
  }
  .product-content__options .options-header {
    min-height: 35%;
    height: auto;
  }
  .product-content__options .options-header__title {
    width: 100%;
    padding-right: 0;
  }
  .product-content__options .options-header__title h1 {
    font-size: 1.4rem;
  }
  .product-content__options .options-header__title h2 {
    font-size: 1.2rem;
  }
  .product-content__options .options-content {
    min-height: 65%;
    height: auto;
  }
  .product-content__options .options-content__top {
    height: 20vh;
    flex-direction: column;
    margin: 1rem 0;
  }
  .product-content__options .options-content__top .product-count {
    width: 100%;
    height: 60%;
    justify-content: center;
  }
  .product-content__options .options-content__top .product-count input {
    font-size: 1.2rem;
  }
  .product-content__options .options-content__top .product-price {
    width: 100%;
    height: 40%;
    justify-content: center;
  }
  .product-content__options .options-content__top .product-price span {
    font-size: 1.2rem;
  }
  .product-content__options .options-content__bottom {
    min-height: 70%;
    height: auto;
    padding: 1rem 0;
  }
  .product-content__options .options-form {
    margin-bottom: 0.5rem;
    flex-direction: column;
  }
  .product-content__options .options-form__label {
    width: 100%;
    height: auto;
    min-height: 1rem;
    padding-right: 0;
    font-size: 1rem;
    text-align: left;
    color: gray;
  }
  .product-content__options .options-form__control {
    width: 100%;
    margin-bottom: 0;
    align-items: flex-start;
    padding-left: 0;
    font-size: 1rem;
    text-align: left;
  }
  .product-content__description {
    width: 100%;
    padding: 0 0 2rem;
  }
  .product-content__description .content-description__map {
    display: none;
    visibility: hidden;
  }
  .product-content__description .content-description__text {
    width: calc(100% - 2rem);
    padding: 0;
  }
}

.map-overlay {
  width: 100%;
  height: 100%;
  display: block;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.5);
}

.product-gallery {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}
.product-gallery img {
  position: absolute;
}
@media (min-width: 1080px) and (max-width: 1680px) {
  .product-gallery__big {
    display: block;
    visibility: visible;
  }
  .product-gallery__medium {
    display: none;
    visibility: hidden;
  }
  .product-gallery__small {
    display: none;
    visibility: hidden;
  }
}
@media (max-width: 1080px) {
  .product-gallery__big {
    display: none;
    visibility: hidden;
  }
  .product-gallery__medium {
    display: block;
    visibility: visible;
  }
  .product-gallery__small {
    display: none;
    visibility: hidden;
  }
}
@media (max-width: 570px) {
  .product-gallery__big {
    display: none;
    visibility: hidden;
  }
  .product-gallery__medium {
    display: none;
    visibility: hidden;
  }
  .product-gallery__small {
    display: block;
    visibility: visible;
  }
}
.product-description {
  width: 100%;
  min-height: 0;
  left: 0;
  top: 0;
  display: flex;
  flex-direction: row;
  background-color: #f2f2f2;
}
.product-description__map {
  width: 50%;
  height: calc(100vh - 100px);
  text-align: center;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}
.product-description__map p {
  color: gray;
  margin: 0;
  position: absolute;
  z-index: 0;
}
.product-description__text {
  width: calc(50% - 10rem);
  min-height: calc(100vh - 100px - 6rem);
  padding: 3rem 5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (max-width: 1080px) {
  .product-description__map {
    display: none;
    visibility: hidden;
  }
  .product-description__text {
    width: 80%;
    padding: 3rem 10%;
  }
}
@media (max-width: 570px) {
  .product-description {
    flex-direction: column;
  }
  .product-description__map {
    display: none;
    visibility: hidden;
  }
  .product-description__text {
    width: calc(100% - 2rem);
    padding: 2rem 1rem;
  }
}

/*=====  End of Product  ======*/
/*================================
=            Category            =
================================*/
.vendor-grid {
  width: calc(100% + 1rem);
  min-height: 100vh;
  margin-left: -1rem;
  background-color: #f2f2f2;
  display: flex;
  flex-wrap: wrap;
}
.vendor-grid__sort {
  width: 100%;
  min-height: 4rem;
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 1rem;
}
.vendor-grid__sort h2 {
  width: calc(100% - 1rem);
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 0 1rem 1rem;
  padding: 1rem 0;
  background-color: #e6e6e6;
}
.vendor-product {
  width: calc(25% - 1rem);
  height: calc(50vh - 0.5rem - 50px);
  background-color: #fff;
  margin-bottom: 1rem;
  margin-left: 1rem;
}
.vendor-product-title {
  width: 100%;
  height: 2rem;
  background-color: #cccccc;
}
.vendor-product__form {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
}
.vendor-product__left {
  width: 90%;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.vendor-product__right {
  width: 10%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #cccccc;
}
.vendor-product__right .adress {
  position: absolute;
  transform: rotate(-90deg);
  font-family: "Proxima Nova";
  font-size: 1.2rem;
  color: gray;
}
.vendor-product__image {
  width: 100%;
  height: 70%;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}
.vendor-product__image img {
  width: 100%;
  height: auto;
  filter: grayscale(1);
  transition: transform 1s cubic-bezier(0.19, 1, 0.22, 1), filter 0.7s;
}
.vendor-product__image:hover img {
  filter: grayscale(0);
  transform: scale(1.05);
}
.vendor-product__title {
  width: calc(100% - 1rem);
  height: 15%;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
  padding: 0 0.5rem;
  background-color: #e6e6e6;
}
.vendor-product__title h3 {
  font-size: 1.2rem;
  text-align: right;
}
.vendor-product__country {
  width: calc(100% - 1rem);
  height: 15%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 0 0.5rem;
  font-size: 1rem;
  text-align: right;
}
.vendor-description {
  width: calc(100% - 100px);
  min-height: 0;
  left: 0;
  top: 0;
  display: flex;
  flex-direction: row;
  position: relative;
  padding: 50px 50px 0;
  background-color: #f2f2f2;
  z-index: 10;
}
.vendor-description__image {
  width: 50%;
  min-height: calc(100vh - 100px);
  text-align: center;
  overflow: hidden;
}
.vendor-description__image img {
  width: auto;
  height: 100%;
}
.vendor-description__title {
  position: absolute;
  padding: 0.75rem 2px 0.5rem 1rem;
  display: inline-block;
  background-color: #f2f2f2;
  left: calc(50% + 2px);
  top: 25%;
  text-align: right;
  transform: translate(-100%, -50%);
}
.vendor-description__title h1 {
  margin: 0;
}
.vendor-description__text {
  width: calc(50% - 10rem);
  min-height: calc(100vh - 100px - 6rem);
  padding: 3rem 5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.vendor-description__breadcrumbs ul.breadcrumb {
  width: 100%;
  height: 2rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  margin: 0;
}
.vendor-description__breadcrumbs ul.breadcrumb li {
  z-index: 1;
}
.vendor-description__breadcrumbs ul.breadcrumb li a {
  font-size: 0.8rem;
