  width: 3rem;
  height: 3rem;
  position: absolute;
  padding: 0.3rem;
  transform: translateX(-3%);
}
.mini-cart__notempty-link #cartIconFull {
  width: 3rem;
  height: 2rem;
  position: absolute;
  padding: 1.8rem 1.8rem 1.2rem 0.2rem;
}
.mini-cart__notempty-link #cartBasket {
  fill: #fff;
  stroke-width: 2.5;
  stroke: #000000;
}
.mini-cart__notempty-link #cartHandle__left {
  fill: none;
  stroke-width: 2;
  stroke: #000000;
  transform-origin: 0 50%;
  animation: cartHandleLeft 0.3s normal;
}
.mini-cart__notempty-link #cartHandle__right {
  fill: none;
  stroke-width: 2;
  stroke: #000000;
  transform-origin: 50% 100%;
  animation: cartHandleRight 0.3s normal;
}
.mini-cart__notempty-link #cartBottle {
  fill: #000000;
  transform: translateX(-5%);
  animation: cartFull 0.3s normal;
}
.mini-cart__notempty-link:hover #cart_back_svg {
  transform: rotate(180deg) translateX(8%);
}
.mini-cart__notempty-count {
  width: 50px;
  height: 50px;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  position: absolute;
  top: 0.7rem;
  z-index: 100;
  font-size: 1.2rem;
  transition: transform 0.3s;
}
