  align-items: center;
  justify-content: flex-start;
  position: relative;
  padding: 0 0.5rem;
  margin-left: 5rem;
}
.mini-cart:hover {
  transform: translate(-8%, -50%);
}
.mini-cart:hover #cart_back_svg {
  transform: rotate(180deg) translateX(32%);
}
.mini-cart:hover .mini-cart__empty,
.mini-cart:hover .mini-cart__notempty {
  transform: translateX(-5%);
}
