    width: 50px;
    height: 50px;
    top: 0;
    transform: translate(0, 0);
  }
  .mini-cart #cart_back_svg {
    display: none;
    visibility: hidden;
  }
  .mini-cart__empty {
    width: 50px;
    height: 50px;
  }
  .mini-cart__empty-link {
    width: 50px;
    height: 50px;
  }
  .mini-cart__empty-link .miniCartIcon {
    width: 40px;
    height: 30px;
    position: absolute;
    padding: 15px 5px 5px 5px;
  }
  .mini-cart__empty-link #cartIconFull {
    width: 30px;
    height: 30px;
    position: absolute;
  }
  .mini-cart__empty-count {
    top: 0;
    font-size: 1rem;
    margin-left: -2px;
  }
  .mini-cart__notempty {
    width: 50px;
    height: 50px;
  }
  .mini-cart__notempty-link {
    width: 50px;
    height: 50px;
  }
  .mini-cart__notempty-link .miniCartIcon {
    width: 40px;
    height: 30px;
    position: absolute;
    padding: 15px 5px 5px 5px;
  }
  .mini-cart__notempty-link #cartIconFull {
    width: 30px;
    height: 30px;
    position: absolute;
  }
