  }
  .mini-cart:hover .mini-cart__empty,
  .mini-cart:hover .mini-cart__notempty {
    transform: translateX(0);
  }
}

@keyframes cartFull {
  0% {
    transform: translate(-5%, -50%) rotate(-10deg);
  }
  100% {
    transform: translate(-5%, 0) rotate(0deg);
  }
}
@keyframes cartHandleLeft {
  0% {
    transform: rotate(40deg) translateY(-10%);
  }
  100% {
    transform: rotate(0) translateY(0);
  }
}
@keyframes cartHandleRight {
  0% {
    transform: rotate(-40deg) translateY(-5%);
  }
  100% {
    transform: rotate(0) translateY(0);
  }
}
