    padding: 3rem;
    align-items: center;
    text-align: center;
  }
}
@media (max-width: 570px) {
  .contact-content__info {
    width: calc(100% - 2rem);
    height: calc(50vh - 50px - 2rem);
    padding: 1rem;
  }
}

