.jGrowl {
    z-index: 9999;
    color: #fff;
    font-size: 12px;
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    position: fixed
}

.jGrowl.top-left {
    left: 0;
    top: 0;
}

.jGrowl.top-right {
    // right: calc(#{$headerWidth} + 0.15rem);
    right: 50px;
    top: 50px;
}

.jGrowl.bottom-left {
    left: 0;
    bottom: 0;
}

.jGrowl.bottom-right {
    right: 0;
    bottom: 0;
}

.jGrowl.top-center {
    top: 50%;
    // width: 50%;
    left: 32.5%;
    transform: translate(-50%, -50%);
}

// .jGrowl.center .jGrowl-closer,
// .jGrowl.center .jGrowl-notification {
//     margin-left: auto;
//     margin-right: auto
// }

.jGrowl-notification {
    width: 15rem;
    min-height: calc(50px - 1rem);
    // background-color: #000;
    opacity: .9;
    // filter: progid: DXImageTransform.Microsoft.Alpha(Opacity=(0.9*100));
    // -ms-filter: progid: DXImageTransform.Microsoft.Alpha(Opacity=(0.9*100));
    zoom: 1;
    padding: 0.5rem;
    // margin: 10px;
    text-align: left;
    display: none;
    // border-radius: 5px;

    // @include grid-media($Small) {
    //     width: calc(100% - 2rem);
    //     min-height: $headerHeightMobile;
    //     padding: 0.5rem 1rem;
    // }

}

.jGrowl-notification .ui-state-highlight,
.jGrowl-notification .ui-widget-content .ui-state-highlight,
.jGrowl-notification .ui-widget-header .ui-state-highlight {
    border: 1px solid #000;
    background: #000;
    color: #fff;
}

.jGrowl-notification .jGrowl-header {
    font-weight: 700;
    font-size: .85em;
}

.jGrowl-notification .jGrowl-close {
    background-color: transparent;
    color: inherit;
    border: none;
    z-index: 99;
    float: right;
    font-weight: 700;
    font-size: 1em;
    cursor: pointer;
}

.jGrowl-closer {
    width: 15rem;
    background-color: #000;
    opacity: .9;
    // filter: progid: DXImageTransform.Microsoft.Alpha(Opacity=(0.9*100));
    // -ms-filter: progid: DXImageTransform.Microsoft.Alpha(Opacity=(0.9*100));
    zoom: 1;
    padding: 0.5rem;
    // margin: 10px;
    display: none;
    // border-radius: 5px;
    // padding-top: 4px;
    padding-bottom: 4px;
    cursor: pointer;
    font-size: .9em;
    font-weight: 700;
    text-align: center;

    @include grid-media($Small) {
        width: calc(100% - 1rem);
    }
}

.jGrowl-closer .ui-state-highlight,
.jGrowl-closer .ui-widget-content .ui-state-highlight,
.jGrowl-closer .ui-widget-header .ui-state-highlight {
    border: 1px solid #000;
    background: #000;
    color: #fff;
}

@media print {
    .jGrowl {
        display: none;
    }
}

div.jGrowl > .ui-state-highlight {
    background: inherit;
    color: inherit;
    border: inherit;
}

.ms2-message-success {
    background-color: $success !important;
}

.ms2-message-error {
    background-color: $error !important;
}

.ms2-message-info {
    background-color: $success !important;
}
